.formation {
    background: map-get($color, black-1);
    color: white;

    .formation-container {
        display: grid;
        grid-template-columns: 1fr 2fr;
        gap: rem(100);
        padding: rem(120) rem(20);
        position: relative;

        .title-break {
            color: black;
            max-width: 5ch;

            /*Responsivo*/
            @include display($tablet-portrait-down) {
                color: map-get($color, white);
                font-size: rem(48);
                text-transform: capitalize;
                max-width: initial;
            }
        }

        .description-formation {
            .paragraph-formation {
                font-size: rem(24);
                line-height: 1.3;
                margin-bottom: rem(60);
                color: map-get($color, white-2);

                strong {
                    color: white;
                }

                /*Responsivo*/
                @include display($tablet-portrait-down) {
                    font-size: rem(18);
                }
            }

            .gradute-formation {
                .gradute {
                    display: flex;
                    flex-wrap: wrap;
                    gap: rem(20);
                    margin-bottom: rem(60);
                    
                    li {
                        flex: 1;
                        background: black;
                        padding: rem(20);
                        border-radius: rem(4);
                        display: flex;
                        flex-direction: column;

                        .degree {
                            font-size: rem(14);
                            text-transform: uppercase;
                            color: map-get($color, white-2);
                            margin-bottom: rem(10);
                        }

                        .university-course {
                            font-size: rem(18);
                            line-height: 1.4;
                            margin-bottom: rem(40);
                            position: relative;
                            flex: 1;
                        }

                        .university-course::before {
                            content: "";
                            display: block;
                            width: rem(4);
                            height: rem(20);
                            background: linear-gradient(map-get($color, verde) , map-get($color, verde-1));
                            position: absolute;
                            left: rem(-24);
                        }

                        .university {
                            font-size: rem(18);
                            color: map-get($color, white-2);
                        }
                    }
                }
            }

            .courses-language-formation {
                margin-bottom: rem(60);

                .title-course {
                    font-size: rem(14);
                    line-height: 1.4;
                    color: map-get($color, cinza-2);
                    text-transform: uppercase;
                    margin-bottom: rem(30);
                    position: relative;
                }

                .title-course::before {
                    content: "";
                    display: block;
                    width: rem(24);
                    height: rem(24);
                    background: url("#{$image-path}cursos.svg");
                    position: absolute;
                    left: rem(-36);
                    top: rem(-4);

                    /*Responsivo*/
                    @include display($tablet-portrait-down) {
                        position: initial;
                        margin-bottom: rem(10);
                    }
                }

                .title-language {
                    font-size: rem(14);
                    line-height: 1.4;
                    color: map-get($color, cinza-2);
                    text-transform: uppercase;
                    margin: rem(60) 0 rem(30);
                    position: relative;
                }

                .title-language::before {
                    content: "";
                    display: block;
                    width: rem(24);
                    height: rem(24);
                    background: url("#{$image-path}idiomas.svg");
                    position: absolute;
                    left: rem(-36);
                    top: rem(-4);

                    /*Responsivo*/
                    @include display($tablet-portrait-down) {
                        position: initial;
                        margin-bottom: rem(10);
                    }
                }

                .courses-formation {
                    li {
                        display: flex;
                        justify-content: space-between;
                        margin-bottom: rem(20);
                    }
                }

                .language-formation {
                    li {
                        margin-bottom: rem(20);
                    }
                }

                .courses-formation, .language-formation {
                    font-size: rem(18);
                    line-height: 1.1;
                    
                    span {
                        color: map-get($color, cinza-2);
                    }
                }
            }
        }
    }

    .formation-container::before {
        content: "";
        display: block;
        width: rem(130);
        height: rem(100);
        background: url("#{$image-path}detalhe.svg") no-repeat center;
        position: absolute;
        bottom: rem(-50);
        left: rem(20);
    }

    /*Resposnvio*/
    @include display($tablet-portrait-down) {
        .formation-container {
            grid-template-columns: 1fr;
            padding: rem(60) rem(20);
        }
    }
}
