.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: rem(40) rem(20);
  margin: 0 auto;

  nav {
      .header-navigation {
          display: flex;
          justify-content: center;
          flex-wrap: wrap;
          gap: rem(10);

            li{
                a{
                    font-size: rem(18);
                    line-height: 1.3;
                    padding: rem(10) rem(20);
                    text-decoration: none;
                    color: map-get($color, black-1);
                    display: block;
                }
            }
        }
    }
    @include display($tablet-portrait-down) {
        flex-direction: column;
        gap: rem(20);
        padding: rem(20);

        nav {
            .header-navigation {
                li {
                    a {
                        background: map-get($color, white-1);
                        border-radius: rem(4);
                    }
                }
            }
        }
    }
}


