.experience {
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: rem(100);

    .title-break {
        color: map-get($color, cinza-1);

        /*Responsivo*/
        @include display($tablet-portrait-down) {
            color: map-get($color, black-1);
            font-size: rem(48);
            text-transform: capitalize;
            max-width: initial;
        }
    }

    .experience-company {
        .paragraph-company {
            font-size: rem(24);
            line-height: 1.3;
            margin-bottom: rem(60);
            color: map-get($color, cinza);
        }

        .company {
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: rem(10) rem(20);
            padding: rem(20);
            background: map-get($color, white-1);
            margin-bottom: rem(20);
            border-radius: rem(4);
            position: relative;

            .year-company {
                position: absolute;
                top: rem(25);
                left: rem(-100);
                color: map-get($color, cinza);
                font-size: rem(14);
                width: rem(80);
                text-align: end;

                /*Responsivo*/
                @include display($tablet-portrait-down) {
                    position: initial;
                    order: 1;
                    width: initial;
                    text-align: start;
                }
            }

            .year-company::before {
                /*Responsivo*/
                @include display($tablet-portrait-down) {
                    content: "ANO: ";
                    display: inline-block;
                    margin: 0 rem(2);

                }
            }

            .title-company {
                font-size: rem(18);
                line-height: 1.1;
                font-weight: bold;
            }

            .paragraph-company {
                font-size: rem(14);
                line-height: 1.4;
                color: map-get($color, cinza);
                margin: 0;
            }

            .ability-company {
                display: flex;
                flex-wrap: wrap;
                gap: rem(10);
                align-content: flex-start;

                li {
                    font-size: rem(14);
                    line-height: 1.4;
                    border-radius: rem(4);
                    background: white;
                    padding: rem(5) rem(10);
                }
            }

            /*Responsivo*/
            @include screen($mobile-only) {
                grid-template-columns: 1fr;
            }
        }

        .company::before {
            content: "";
            display: block;
            width: rem(4);
            height: rem(20);
            background: linear-gradient(map-get($color, verde), map-get($color, verde-1));
            position: absolute;
            top: rem(20);
        }
    }

    /*Responsivo*/
    @include display($tablet-portrait-down) {
        grid-template-columns: 1fr;
        gap: rem(40);
    }
}