.footer {
 background: black; 

 .footer-container {
    padding: rem(120) rem(20) rem(60) rem(20);
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: rem(100);
    align-items: center;

    .footer-description {
        font-size: rem(24);
        line-height: 1.33;
        color: map-get($color, white-3);
    }

    .footer-contact {
        font-weight: bold;
        font-size: rem(36);
        line-height: 1.1;
        color: white;
        margin-bottom: rem(30);
        margin: 0 auto;

        li {
            padding: rem(10);
            a {
                color: white;
                text-decoration: none;
            }
        }

        @include display($tablet-portrait-down) {
            font-size: rem(24);
        }
    }

    .footer-copy {
        font-size: rem(18);
        color: map-get($color, cinza-2);
        grid-column: 1 / -1;
    }

    @include display($tablet-portrait-down) {
        grid-template-columns: 1fr;
        gap: rem(40);
    }
 }
}
 