.introduction {
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: rem(100);
    padding: rem(40) rem(20);
    align-items: center;
    justify-items: center;

    .text-introduction {
        .title-introduction {
            font-size: rem(72);
            font-family: $fira-sans-bold;
            line-height: 1.125;
            margin-bottom: rem(30);
            position: relative;

            /*Responsivo*/
            @include display($tablet-landscape-down) {
                font-size: rem(48);
            }

            @include display($tablet-portrait-down) {
                font-size: rem(32);
            }
        }

        @include screen($mobile-only) {
            margin: 0 auto;
        }

        .title-introduction::before {
            content: "";
            display: block;
            width: rem(130);
            height: rem(100);
            background: url("#{$image-path}detalhe.svg") no-repeat center;
            position: absolute;
            top: rem(-15);
            left: rem(-40);
            z-index: -1;

            /*Responsivo*/
            @include display($tablet-landscape-down) {
                top: rem(-20);
            }

            @include display($tablet-portrait-down) {
                width: rem(30);
                height: rem(10);
                left: 0;
            }
        }

        .paragraph-introduction {
            font-size: rem(24);
            color: map-get($color, cinza);
            
            /*Responsivo*/
            @include display($tablet-portrait-down) {
                font-size: rem(18);
            }
        }
    }

    /*Responsivo*/
    @include display($tablet-landscape-down) {
        gap: 0;
    }

    @include display($tablet-portrait-down) {
        grid-template-columns: 1fr 1fr;
        gap: rem(40);
    }

    @include screen($mobile-only) {
        grid-template-columns: 1fr;

        .profile img {
            display: none;
        }
    }
}