@font-face {
  font-family: 'Font Name';
  src: url('#{$font-path}subset-FontName.eot');
  src: url('#{$font-path}subset-FontName.eot?#iefix') format('embedded-opentype'),
      url('#{$font-path}subset-FontName.woff2') format('woff2'),
      url('#{$font-path}subset-FontName.woff') format('woff'),
      url('#{$font-path}subset-FontName.ttf') format('truetype'),
      url('#{$font-path}subset-FontName.svg#FontName') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'FiraSans-Bold';
  src: url('#{$font-path}subset-FiraSans-Bold.eot');
