      url('#{$font-path}subset-FiraSans-Bold.ttf') format('truetype'),
      url('#{$font-path}subset-FiraSans-Bold.svg#FiraSans-Bold') format('svg');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

